:root{
    --primary-color: #047aed;
}

body{
    width:100%;
    height:100vh;
    background-color: cadetblue;
    /*background-color: azure; */
}

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

svg:hover{
  cursor: pointer;
}

.background{
    position: relative;
    display: grid;
    justify-content: center;
    text-align: -webkit-center;
    font-family: sans-serif;
    padding: 20px 0px;
    padding-left: calc(100vw - 100%);
    height: 100vh;
}

.master-container{
    display: flex;
    gap: 10px;
    flex-direction: column;
    justify-content: center;

}

.react-calendar { 
  width: 500px;
  max-width: 100%;
  /* background-color: rgb(131, 125, 125); */
  /* color: white; */
  border-radius: 8px;
  /* box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2); */
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.125em;
   }
  
   .react-calendar__navigation button {
    min-width: 44px;
    background:none;
    font-size: 16px;
    margin-top: 8px;
   }
  
  .react-calendar--doubleView {
    width: 700px;
  }
  .react-calendar--doubleView .react-calendar__viewContainer {
    display: flex;
    margin: -0.5em;
  }
  .react-calendar--doubleView .react-calendar__viewContainer > * {
    width: 50%;
    margin: 0.5em;
  }
  .react-calendar,
  .react-calendar *,
  .react-calendar *:before,
  .react-calendar *:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
  .react-calendar button {
    font-weight: 600;
    margin: 0;
    border: 0;
    outline: none;
  }
  .react-calendar button:enabled:hover {
    cursor: pointer;
  }
  .react-calendar__navigation {
    display: flex;
    height: 44px;
    margin-bottom: 1em;
  }
  .react-calendar__navigation button {
    min-width: 44px;
    /* background: none; */
  }
  .react-calendar__navigation button:enabled:hover,
  .react-calendar__navigation button:enabled:focus {
    background-color: #e6e6e6;
  }
  .react-calendar__navigation button[disabled] {
    background-color: #f0f0f0;
  }
  .react-calendar__month-view__weekdays {
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 0.75em;
  }
  .react-calendar__month-view__weekdays__weekday {
    padding: 0.5em;
  }
  .react-calendar__month-view__weekNumbers .react-calendar__tile {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.75em;
    font-weight: bold;
    padding: calc(0.75em / 0.75) calc(0.5em / 0.75);
  }
  .react-calendar__month-view__days__day--weekend {
    /* color: #d10000; */
  }
  .react-calendar__month-view__days__day--neighboringMonth {
    color: white;
  }
  .react-calendar__year-view .react-calendar__tile,
  .react-calendar__decade-view .react-calendar__tile,
  .react-calendar__century-view .react-calendar__tile {
    padding: 2em 0.5em;
  }
  .react-calendar__tile {
    max-width: 100%;
    text-align: center;
    padding: 0.75em 0.5em;
    background: none;
  }
  .react-calendar__tile:disabled {
    background-color: #f0f0f0;
  }
  .react-calendar__tile:enabled:hover,
  .react-calendar__tile:enabled:focus {
    background-color: #e6e6e6;
  }
  .react-calendar__tile--now {
    /* background: #ffff76; */
    background-color: black
  }
  .react-calendar__tile--now:enabled:hover,
  .react-calendar__tile--now:enabled:focus {
    background: #ffffa9;
  }
  .react-calendar__tile--hasActive {
    background: #76baff;
  }
  .react-calendar__tile--hasActive:enabled:hover,
  .react-calendar__tile--hasActive:enabled:focus {
    background: #a9d4ff;
  }
  .react-calendar__tile--active {
    background: black;
    color: white;
  }
  .react-calendar__tile--active:enabled:hover,
  .react-calendar__tile--active:enabled:focus {
    background: #1087ff;
  }
  .react-calendar--selectRange .react-calendar__tile--hover {
    background-color: #e6e6e6;
  }

.container{
  background: lightblue;
  gap: 3rem;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  text-align: -webkit-center;
  font-family: sans-serif;
  padding: 3rem;
  justify-items: center;
  flex-direction: column;
  align-items: center;
}

.todo-list-main-container{
  /* width: 100%; */
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.todo-form-container{
  width: 100%;
}

.todoList{
    position: relative;
    display: grid;
    text-align: -webkit-center;
    font-family: sans-serif;
    justify-content: center;
    width: 100%;
   
}

.todoItems{
 
  display: grid;
  grid-template-columns: 20px 300px 40px 40px;
  grid-gap: 27px;
  padding: 20px;
  align-content: center;
  align-items: center;
  justify-content: center;
  justify-items: start;
}

.todoItems-sub{
  /*
  display: flex;
  grid-gap: 20px;
  padding-top: 30px;
  padding-bottom: 40px;
  height: 50px;
  font-size: larger;
  align-content: center;
  align-items: center;
  */
  display: grid;
  grid-template-columns: 20px 280px 40px 40px;
  grid-gap: 27px;
  padding-top: 10px;
  padding-bottom: 20px;
  height: 50px;
  font-size: larger;
  align-content: center;
  align-items: center;
  justify-items: start;
  justify-content: center;
}
.todo-form{
  width: 100%;
  display: flex;
  background-color: lightblue;
  align-items: center;
  justify-content: center;
}



.showmore{
    display: block;
    padding: 10px;
    font-weight: bold;
    background-color: transparent;
    cursor: pointer;
    overflow: hidden;
    outline: none;
}

input[type=checkbox]{
    transform: scale(1.5);
}

/* input[type=text]{
    transform: scale(1.15);
} */

.task{
    font-size: 18px;
}

button[type=submit]{
  /* transform: scale(1.15); */
  width: 25%;
  padding: 10px;
  font-size: 18px;
  background: aliceblue;
  border-style: groove;
  border-width: 1px;
}

.enterField{

    border:none;
    background: none;
    font-size: 18px;
    padding-left: 50px;

}

input.enterField:focus {
    outline-width: 0;
}

.todo-add-container{
    display: flex;
    justify-content: center;
    width: 520px;
}

input.enterAddField:focus {
    outline-width: 0;
}



.todo-edit-container{
    display: grid;
    grid-template-columns: 375px 55px 20px;
    gap: 15px;
    font-size: larger;
    background-color: mintcream;
    border: black;
    border-style: solid;
    border-width: thin;
    border-radius: 5px;
    width: 100%;
    padding: 20px;
    align-content: center;
    align-items: center;
    justify-content: center;
    justify-items: start;
    
}

.todoHolder{
    
    background-color: mintcream;
    border: black;
    border-style: solid;
    border-width: thin;
    border-radius: 5px;

}


.todo-items-container{
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    width: 100%;
    flex-wrap: nowrap;
    gap: 5px;
    align-items: center;
}

.enterAddField{
  width: 100%;
  font-size: 18px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 15px;
  border-style: groove;
  border-right: none;
  border-width: 1px;
}

.sendButton:hover{
  cursor: pointer;
}


@media only screen and (max-width: 1300px){

  .container{
    width: 350px;
    justify-items: center;
  }

  .react-calendar{
    width: 250px;
  }

  .todo-list-main-container{
    align-items: center;
  }

  .todo-form-container{
    width: 300px;
  }

  .todoItems{
    width: 300px;
    grid-template-columns: 20px 100px 20px 20px;
  }

  .todo-edit-container{
    grid-template-columns: 210px 40px 30px;
    gap: 5px;
  }

  .enterField{
    padding-left: 72px;
  }

}